html {}

body {
    margin: 0;
}

.container {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

ul {
    list-style: none;
    padding: 0;
}

a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

@media (min-width: 1114px) {
    .container {
        max-width: 1114px;
        margin: auto;
    }
}